rmado sobre la política comercial puede adaptar su enfoque de ventas para cumplir con las expectativas de los clientes, mientras contribuye a los objetivos de la entidad, como el crecimiento de la cartera de clientes, la fidelización y la mitigación de riesgos.")])])])]),_c('div',{staticClass:"py-3 py-lg-4",attrs:{"titulo":"Cumplimiento de la normatividad legal vigente"}},[_c('div',{staticClass:"row p-3"},[_c('div',{staticClass:"col-lg-4 col-12 mb-4 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema5/img_3.png"),"alt":""}})])]),_c('div',{staticClass:"col-lg-8 col-12"},[_c('h4',[_vm._v("Cumplimiento de la normatividad legal vigente")]),_c('p',[_vm._v("La normatividad legal en el sector financiero es compleja y en constante evolución. Incluye regulaciones nacionales e internacionales que rigen aspectos como la protección al consumidor, la transparencia en las transacciones, la prevención del lavado de dinero, y el cumplimiento tributario, entre otros. Un asesor comercial debe estar actualizado y comprender en profundidad estas normativas para asegurar que las operaciones realizadas no solo sean efectivas desde el punto de vista comercial, sino también legales y éticas. Esto no solo protege a la entidad de posibles sanciones, sino que también refuerza la confianza del cliente en la entidad. Cuando un asesor demuestra un sólido conocimiento de la normativa, puede guiar al cliente con seguridad y precisión, asegurando que cada transacción cumpla con las leyes aplicables y con las políticas internas de la entidad.")])])])]),_c('div',{staticClass:"py-3 py-lg-4",attrs:{"titulo":"Enriquecimiento continuo del asesor"}},[_c('div',{staticClass:"row p-3"},[_c('div',{staticClass:"col-lg-4 col-12 mb-4 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema5/img_4.png"),"alt":""}})])]),_c('div',{staticClass:"col-lg-8 col-12"},[_c('h4',[_vm._v("Enriquecimiento continuo del asesor")]),_c('p',[_vm._v("Además de cumplir con la política comercial y la normatividad legal, es fundamental que el asesor comercial busque un enriquecimiento continuo de su conocimiento y habilidades. Esto implica estar al tanto de las últimas tendencias en el mercado financiero, así como de las actualizaciones en las políticas y normativas que puedan afectar su trabajo. La formación continua y el desarrollo profesional son esenciales para que el asesor no solo se mantenga competente, sino que también pueda ofrecer un valor agregado a sus clientes. Un asesor bien informado y capacitado puede anticipar cambios en el entorno regulatorio y comercial, y ajustar su enfoque en consecuencia, lo que le permite proporcionar un servicio más completo y eficaz.")])])])]),_c('div',{staticClass:"py-3 py-lg-4",attrs:{"titulo":"Impacto en la satisfacción del cliente y el cumplimiento normativo"}},[_c('div',{staticClass:"row p-3"},[_c('div',{staticClass:"col-lg-4 col-12 mb-4 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema5/img_5.png"),"alt":""}})])]),_c('div',{staticClass:"col-lg-8 col-12"},[_c('h4',[_vm._v("Impacto en la satisfacción del cliente y el cumplimiento normativo")]),_c('p',[_vm._v("Cuando un asesor comercial combina sus cualidades personales con un profundo conocimiento de la política comercial y la normatividad vigente, el impacto en la satisfacción del cliente es significativo. El cliente no solo recibe un servicio que está alineado con sus expectativas y necesidades, sino que también puede estar seguro de que todas las transacciones y recomendaciones cumplen con las regulaciones aplicables. Esto refuerza la confianza del cliente en la entidad financiera y contribuye a una relación a largo plazo basada en la transparencia, la legalidad y la satisfacción mutua.")])])])])]),_c('Separador'),_vm._m(2),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-left"}},[_vm._v("El asesor comercial de una entidad financiera es una persona experimentada de la empresa, que resolverá todas las dudas y e