,{staticClass:"mb-5",attrs:{"data-aos":"fade-left"}},[_vm._v("Las personas encargadas de dar asesoría pueden ocupar cargos bastante diversos dentro de la organización o las entidades financieras y desarrollar otras funciones además de las aquí consideradas. En cualquier caso, un asesor comercial desarrolla una serie de funciones que se describen a continuación:")]),_vm._m(6),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-left"}},[_vm._v("Para ejercer la función de asesor comercial no es necesario disfrutar de ninguna cualidad especial. No obstante, para llegar a tener éxito en el desempeño, hay que poseer ciertas características personales, así como un conjunto de actitudes profesionales, sin las cuales todo esfuerzo de venta y atención al cliente estará condenado a la mediocridad. Las cualidades del asesor deben ser:")]),_vm._m(7),_vm._m(8),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-left"}},[_vm._v("Un asesor comercial para desempeñarse con éxito necesita recibir unos conocimientos específicos y una formación profesional que le permitan desenvolverse sin problemas en las diversas situaciones que se le pueden plantear.")]),_vm._m(9),_c('Separador'),_vm._m(10),_vm._m(11),_c('div',{staticClass:"fondo-3 tarjeta p-4 mb-5",attrs:{"data-aos":"fade-down"}},[_c('LineaTiempoC',{staticClass:"color-acento-botones"},[_c('div',{staticClass:"row",attrs:{"titulo":"I"}},[_c('div',{staticClass:"col-lg-6 col-12 mb-4 mb-lg-0"},[_c('h4',[_vm._v("Conoce a tu cliente")]),_c('p',[_vm._v("El primer paso para una asesoría comercial exitosa es conocer bien al cliente. Esto implica realizar una investigación previa y recopilar toda la información posible sobre el historial financiero, las necesidades, y las expectativas del cliente. Utiliza herramientas como el CRM ("),_c('em',[_vm._v("Customer Relationship Management")]),_vm._v(") para tener un perfil detallado del cliente antes de la reunión. Conocer a tu cliente te permitirá ofrecerle soluciones más personalizadas y relevantes,